const WEB_AR_ERROR = () => {
    window.addEventListener('webarerror', (event) => {
        console.log(`WebAR error: ${event.code} msg: ${event.errorMessage} `);
    });
};

const WEB_AR_ENGINE_STATUS_CHANGE = (start, loading, success, fail) => {
    window.addEventListener('arenginestatuschange', (event) => {
        if (event.status === 'DOWNLOADSTART') {
            if (start) {
                start();
            }
        } else if (event.status === 'DOWNLOADING') {
            if (loading) {
                loading();
            }
        } else if (event.status === 'SUCCESS') {
            if (success) {
                success();
            }
        } else if (event.status === 'FAILED') {
            if (fail) {
                fail();
            }
        }
    });
};

export { WEB_AR_ERROR, WEB_AR_ENGINE_STATUS_CHANGE };


