import LayaEngine from './LayaEngine';
import ThreeEngine from './ThreeEngine';

class EngineFactory {
    static create(name, args) {
        let engine = null;
        if (name === 'Laya') {
            engine = new LayaEngine(args);
        } else if (name === 'Three') {
            engine = new ThreeEngine(args);
        }
        return engine;
    }
}

export default EngineFactory;


