import Loader from './Loader';
import EngineFactory from './EngineFactory';

class Engine extends Loader {
    constructor(onComplete, onProgress, onStart) {
        super(onComplete, onProgress, onStart);
        this.engine = null;
        this.cameraTransformChange = false;
    }

    create(name, args) {
        this.engine = EngineFactory.create(name, args);
    }

    load(vrDisplay) {
        if (this.onStart) {
            this.onStart();
        }

        this.engine.load(vrDisplay);

        if (this.onComplete) {
            this.onComplete();
        }
    }

    onCameraTransformChange() {
        if (this.cameraTransformChange) {
            this.engine.onCameraTransformChange();
        }
    }

    setCameraTransformChange(flag) {
        this.cameraTransformChange = flag;
    }

    getScene() {
        return this.engine.getScene();
    }

    getCamera() {
        return this.engine.getCamera();
    }
}

export default Engine;


