/*
*  base class for load classes
* */

class Loader {
    constructor(onComplete, onProgress, onStart) {
        this.onStart = onStart || function() {};
        this.onProgress = onProgress || function() {};
        this.onComplete = onComplete || function() {};
    }

    /*
    * virtual method. need to be override
    * */
    load() {
        // console.log('virtual method. Need to be override!');
    }
}

export default Loader;


