/**
 * @file Finite  state machine ()
 * @author: chalreeshen
 * @Date 2018-7-11 14:36:48
 */
class Node {
    constructor(name) {
        this.name = name;
    }

    compare(name) {
        return this.name === name;
    }

    getName() {
        return this.name;
    }

    setName(name) {
        this.name = name;
    }
}

class FSM {
    constructor(names) {
        // 
        this.nodes = [];
        for (let i = 0; i < names.length; i += 1) {
            const node = new Node(names[i]);
            this.nodes.push(node);
        }

        // 
        [this.current] = this.nodes;

        // 
        this.transitionMatrix = [];
    }

    transition(from, to, method) {
        this.transitionMatrix.push({
            from,
            to,
            method
        });
    }

    transitions(arr) {
        for (let i = 0; i < arr.length; i += 1) {
            let [arr1, arr2] = [[], []];
            if (arr[i].from instanceof Array) {
                arr1 = arr[i].from;
            } else {
                arr1 = [arr[i].from];
            }

            if (arr[i].to instanceof Array) {
                arr2 = arr[i].to;
            } else {
                arr2 = [arr[i].to];
            }
            for (let k = 0; k < arr1.length; k += 1) {
                for (let z = 0; z < arr2.length; z += 1) {
                    this.transition(arr1[k], arr2[z], arr[i].method);
                }
            }
        }
    }

    getNode(name) {
        for (let i = 0; i < this.nodes.length; i += 1) {
            if (this.nodes[i].compare(name)) {
                return this.nodes[i];
            }
        }
        return null;
    }

    setCurrent(name) {
        for (let i = 0; i < this.nodes.length; i += 1) {
            if (this.nodes[i].compare(name)) {
                this.current = this.nodes[i];
                return true;
            }
        }
        return false;
    }

    getCurrent() {
        return this.current.getName();
    }

    move(to) {
        if (this.current.compare(to)) {
            return false;
        }

        for (let i = 0; i < this.transitionMatrix.length; i += 1) {
            if (this.current.compare(this.transitionMatrix[i].from)) {
                if (this.transitionMatrix[i].to === to) {
                    this.setCurrent(to);
                    if (typeof this.transitionMatrix[i].method === 'function') {
                        this.transitionMatrix[i].method();
                    }
                }
            }
        }
    }
}

export default FSM;


