import { ANDROID_PLATFORM } from '../tools/Env';

/* global THREE */
class ThreeEngine {
    static inited = false;

    constructor() {
        if (!ThreeEngine.inited) {
            ThreeEngine.init();
        }

        this.vrDisplay = null;
        this.render = null;
        this.scene = null;
        this.camera = null;
        this.vrControls = null;
        this.arview = null;
    }

    static init() {
        this.render = new THREE.WebGLRenderer({ alpha: true });
        this.render.setPixelRatio(window.devicePixelRatio);
        this.render.setSize(window.innerWidth, window.innerHeight);
        document.body.appendChild(this.render.domElement);
        this.scene = new THREE.Scene();
        // this.camera = new THREE.Camera();
    }

    load(vrDisplay) {
        this.camera = new THREE.ARPerspectiveCamera(
            vrDisplay,
            60,
            window.innerWidth / window.innerHeight,
            0.01,
            10000
        );
        this.vrControls = new THREE.VRControls(this.camera);
        this.arview = new ARView(vrDisplay, this.render);
        if (ANDROID_PLATFORM) {
            this.arView.render();
        }
    }
}

export default ThreeEngine;


