import { ANDROID_PLATFORM } from '../tools/Env';

class Marker {
    constructor(vrDisplay) {
        this.vrDisplay = vrDisplay;
        this.markers = new Map();
    }

    /**
     * add marker
     *
     * @param {object} options
     * @param {string} options.url url
     * @param {string} options.name marker
     * @return {promise} promise
     */
    async addMarker(options) {
        // if (IOS_PLATFORM) {
        //     await this.addMarkerInIOS(options);
        // }

        if (ANDROID_PLATFORM) {
            await this.addMarkerInAndroid(options);
        }
    }

    /**
     * add marker in android
     *
     * @param {object} options
     * @param {string} options.url url
     * @param {string} options.name marker
     * @return {promise}
     */
    addMarkerInAndroid(options) {
        const promise = new Promise((resolve, reject) => {
            const img = new Image();
            img.src = options.url;
            img.onload = () => {
                const id = this.vrDisplay.addMarker(img);
                if (id === 0) {
                    resolve(new Error('Fail to add marker'));
                } else {
                    this.markers.set(options.name, {
                        id,
                        url: options.url
                    });
                    resolve();
                }
            };
            img.onerror = () => {
                reject(new Error(`code = -1, msg = ${options.url} load error`));
            };
        });
        return promise;
    }

    /**
     * add marker in ios
     *
     * @param {object} options
     * @param {string} options.url url
     * @param {string} options.name marker
     * @return {promise}
     */
    // addMarkerInIOS(options) {
    //     const promise = new Promise((resolve, reject) => {
    //         if (browser && browser.ar && browser.ar.addARMarkerWithURL) {
    //             browser.ar.addARMarkerWithURL(
    //                 {
    //                     name: options.name,
    //                     url: options.url,
    //                     type: options.type || 0
    //                 },
    //                 (params) => {
    //                     if (params && params.success) {
    //                         this.markers.set(options.name, {
    //                             url: options.url
    //                         });
    //                         resolve();
    //                     } else {
    //                         reject(
    //                             new Error(`code = ${params.errorCode}, msg = ${params.message}`)
    //                         );
    //                     }
    //                 }
    //             );
    //         } else {
    //             reject(new Error(`code = -1, msg = addARMarkerWithURL undefined`));
    //         }
    //     });
    //     return promise;
    // }

    removeMarker(name) {
        // if (IOS_PLATFORM) {
        //     if (browser && browser.ar) {
        //         browser.ar.removeARMarker(name);
        //     }
        // }

        if (ANDROID_PLATFORM) {
            const val = this.markers.get(name);
            if (val.id) {
                this.vrDisplay.removeMarker(val.id);
            }
        }
    }

    setVrDisplay(vrDisplay) {
        this.vrDisplay = vrDisplay;
    }
}

export default Marker;


