/**
 * @file Observer 
 * @author: chalreeshen
 * @Date 2018-7-11 14:36:48
 */
class Observer {
    constructor() {
        this.subscribes = new Map();
    }

    on(topic, func) {
        if (this.subscribes.has(topic)) {
            const arr = this.subscribes.get(topic);
            arr.push(func);
        } else {
            this.subscribes.set(topic, [func]);
        }
    }

    remove(topic) {
        this.subscribes.delete(topic);
    }

    trigger(topic, message) {
        if (this.topic.has(topic)) {
            const funcs = this.topic.get(topic);
            for (const func of funcs) {
                func(message);
            }
        }
    }

    triggerAll() {
        for (const funcs of this.subscribes.values()) {
            for (const func of funcs) {
                func();
            }
        }
    }
}

export default Observer;


