/**
 * @file AR VR Display AR
 * @author: chalreeshen
 * @Date 2018-7-11 14:36:48
 */
import FSM from './FSM';
import { IOS_PLATFORM } from '../tools/Env';
import { WEB_AR_ERROR } from '../tools/Watch';

class AR {
    constructor(render) {
        this.vrDisplay = null;
        this.render = render;

        // android AR
        if (IOS_PLATFORM) {
            this.engineDownload = true;
        } else {
            this.engineDownload = false;
        }

        //  AR
        this.fsm = new FSM(['notAvailable', 'limited', 'normal']);
        this.notAvailable2LimitedFunc = null;
        this.notAvailable2NormalFunc = null;
        this.limited2NormalFunc = null;
        this.limited2NotAvailableFunc = null;
        this.normal2LimitedFunc = null;
        this.normal2NotAvailableFunc = null;

        // 
        this.fsm.transitions([
            {
                from: 'notAvailable',
                to: 'limited',
                method: () => {
                    if (typeof this.notAvailable2LimitedFunc === 'function') {
                        this.notAvailable2LimitedFunc();
                    }
                }
            },
            {
                from: 'notAvailable',
                to: 'normal',
                method: () => {
                    if (typeof this.notAvailable2NormalFunc === 'function') {
                        this.notAvailable2NormalFunc();
                    }
                }
            },
            {
                from: 'limited',
                to: 'normal',
                method: () => {
                    if (typeof this.limited2NormalFunc === 'function') {
                        this.limited2NormalFunc();
                    }
                }
            },
            {
                from: 'limited',
                to: 'notAvailable',
                method: () => {
                    if (typeof this.limited2NotAvailableFunc === 'function') {
                        this.limited2NotAvailableFunc();
                    }
                }
            },
            {
                from: 'normal',
                to: 'limited',
                method: () => {
                    if (typeof this.normal2LimitedFunc === 'function') {
                        this.normal2LimitedFunc();
                    }
                }
            },
            {
                from: 'normal',
                to: 'notAvailable',
                method: () => {
                    if (typeof this.normal2NotAvailableFunc === 'function') {
                        this.normal2NotAvailableFunc();
                    }
                }
            }
        ]);

        if (IOS_PLATFORM) {
            this.onTrackingStateChanged();
        }

        WEB_AR_ERROR();
    }

    /**
     *  android only
     */
    static initAREngine(options, success, fail) {
        const type = options.type || 1;
        if (!navigator.initAREngine) {
            fail();
        } else {
            navigator.initAREngine(type).then((result) => {
                if (result.success) {
                    success();
                } else {
                    fail();
                }
            });
        }
    }

    setNotAvailable2LimitedFunc(callback) {
        this.notAvailable2LimitedFunc = callback;
    }

    setNotAvailable2NormalFunc(callback) {
        this.notAvailable2NormalFunc = callback;
    }

    setLimited2NormalFunc(callback) {
        this.limited2NormalFunc = callback;
    }

    setNormal2LimitedFunc(callback) {
        this.normal2LimitedFunc = callback;
    }

    setLimited2NotAvailableFunc(callback) {
        this.limited2NotAvailableFunc = callback;
    }

    setNormal2NotAvailableFunc(callback) {
        this.normal2NotAvailableFunc = callback;
    }

    /*
    * override virtual load method in base class
    * get vrDisplay object
    * */
    load() {
        const self = this;
        return this.getARDisplay().then((display) => {
            if (display) {
                self.vrDisplay = display;
                return display;
            }
            throw new Error('get vrDisplay error!');
        });
    }

    getARDisplay() {
        return new Promise((resolve) => {
            if (!navigator.getVRDisplays) {
                resolve(null);
                return;
            }

            navigator.getVRDisplays().then((displays) => {
                if (!displays && displays.length === 0) {
                    resolve(null);
                    return;
                }
                for (const display of displays) {
                    if (display.displayName.toLowerCase().includes('tango')) {
                        resolve(display);
                        return;
                    }
                }
                resolve(displays[0]);
            });
        });
    }

    /*
    * get vrDisplay object
    * */
    getVRDisplay() {
        return this.vrDisplay;
    }

    /*
    * tracking state changed event ( ios only )
    * */
    onTrackingStateChanged() {
        const self = this;
        window.onTrackingStateChanged = function(data) {
            const state = Number.parseInt(data.state, 10);
            if (state === 2) {
                self.fsm.move('normal');
            } else if (state === 1) {
                self.fsm.move('limited');
            } else if (state === 0) {
                self.fsm.move('notAvailable');
            }
        };
    }

    /*
    * tracking state changed event (android only)
    * */
    onTarStateChanged(state) {
        if (this.fsm) {
            console.log(`from ${this.fsm.getCurrent()} ---> to : ${state}`);
            this.fsm.move(state);
        }
    }

    /*
    * get finite state machine
    * */
    getFSM() {
        return this.fsm;
    }

    /*
    * get finite state machine current state
    * */
    getCurrentState() {
        return this.fsm.getCurrent();
    }

    /**
     * 
     */
    isEngineDownload() {
        return this.engineDownload;
    }

    setEngineDownload(flag) {
        this.engineDownload = flag;
    }
}

export default AR;


