import Observer from './Observer';
import { ANDROID_PLATFORM, IOS_PLATFORM } from '../tools/Env';

class Render {
    constructor() {
        this.vrDisplay = null;
        this.observer = new Observer();
    }

    /*
    * ios  android.
    * */
    init() {
        const self = this;
        const windowRequestAnimationFrame = window.requestAnimationFrame;

        if (ANDROID_PLATFORM) {
            window.requestAnimationFrame = function(callback) {
                if (self.vrDisplay != null) {
                    self.vrDisplay.requestAnimationFrame(callback);
                } else {
                    windowRequestAnimationFrame(callback);
                }
            };
        }

        function callbacks() {
            self.observer.triggerAll();
            if (ANDROID_PLATFORM) {
                if (self.vrDisplay != null) {
                    self.vrDisplay.requestAnimationFrame(callbacks);
                } else {
                    windowRequestAnimationFrame(callbacks);
                }
            }
            if (IOS_PLATFORM) {
                windowRequestAnimationFrame(callbacks);
            }
        }

        callbacks();
    }

    /*
    * set vr display
    * */
    setVRDisplay(vrDisplay) {
        this.vrDisplay = vrDisplay;
    }

    /*
    * register
    * */
    on(topic, func) {
        this.observer.on(topic, func);
    }
}

export default Render;


