import { ANDROID_PLATFORM } from '../tools/Env';
import ARView from './ARView';

/* global Laya Laya3D VRFrameData */
/* eslint no-underscore-dangle: ["error", { "allow":
 [
    "_useProgram",
    "_renderScene",
    "_bindActive"
]}] */
class LayaEngine {
    static inited = false;

    constructor() {
        if (!LayaEngine.inited) {
            LayaEngine.init();
        }

        this.scene = new Laya.Scene();
        this.camera = new Laya.Camera(0, 0.1, 10000);
        this.scene.addChild(this.camera);

        this.gl = null;
        this.arView = null;
        this.vrDisplay = null;
    }

    /**
     *  
     */
    static init() {
        Laya3D.init(0, 0, true, true);
        Laya.stage.scaleMode = Laya.Stage.SCALE_FULL;
        Laya.stage.screenMode = Laya.Stage.SCREEN_NONE;
        Laya.stage.bgColor = 'none';
        LayaEngine.inited = true;
    }

    load(vrDisplay) {
        Laya.stage.addChild(this.scene);
        this.vrDisplay = vrDisplay;
        const self = this;

        // 
        if (ANDROID_PLATFORM) {
            this.gl = Laya.WebGL.mainContext;
            this.arView = new ARView(vrDisplay, this.gl);
            Laya.WebGL.mainContext.useProgram(Laya.WebGLContext._useProgram);
            this.scene.oldRenderScene = this.scene._renderScene;
            this.scene._renderScene = function(p0, p1) {
                if (self.arView != null) {
                    Laya.WebGLContext.setDepthTest(Laya.WebGL.mainContext, false);
                    Laya.WebGLContext.setBlend(Laya.WebGL.mainContext, false);
                    Laya.WebGLContext.setCullFace(Laya.WebGL.mainContext, false);
                    self.arView.render();
                    Laya.WebGL.mainContext.useProgram(Laya.WebGLContext._useProgram);
                    if (Laya.Buffer._bindActive[Laya.WebGL.mainContext.ARRAY_BUFFER]) {
                        Laya.WebGL.mainContext.bindBuffer(
                            Laya.WebGL.mainContext.ARRAY_BUFFER,
                            Laya.Buffer._bindActive[Laya.WebGL.mainContext.ARRAY_BUFFER]
                        );
                    }
                    if (Laya.Buffer._bindActive[Laya.WebGL.mainContext.ELEMENT_ARRAY_BUFFER]) {
                        Laya.WebGL.mainContext.bindBuffer(
                            Laya.WebGL.mainContext.ELEMENT_ARRAY_BUFFER,
                            Laya.Buffer._bindActive[Laya.WebGL.mainContext.ELEMENT_ARRAY_BUFFER]
                        );
                    }
                }
                this.scene.oldRenderScene(p0, p1);
            };
        }
    }

    onCameraTransformChange() {
        const frameData = new VRFrameData();
        this.vrDisplay.getFrameData(frameData);

        this.camera.projectionMatrix = new Laya.Matrix4x4(
            frameData.leftProjectionMatrix[0],
            frameData.leftProjectionMatrix[1],
            frameData.leftProjectionMatrix[2],
            frameData.leftProjectionMatrix[3],
            frameData.leftProjectionMatrix[4],
            frameData.leftProjectionMatrix[5],
            frameData.leftProjectionMatrix[6],
            frameData.leftProjectionMatrix[7],
            frameData.leftProjectionMatrix[8],
            frameData.leftProjectionMatrix[9],
            frameData.leftProjectionMatrix[10],
            frameData.leftProjectionMatrix[11],
            frameData.leftProjectionMatrix[12],
            frameData.leftProjectionMatrix[13],
            frameData.leftProjectionMatrix[14],
            frameData.leftProjectionMatrix[15]
        );
        this.camera.transform.position = new Laya.Vector3(
            frameData.pose.position[0],
            frameData.pose.position[1],
            frameData.pose.position[2]
        );
        this.camera.transform.rotation = new Laya.Quaternion(
            frameData.pose.orientation[0],
            frameData.pose.orientation[1],
            frameData.pose.orientation[2],
            frameData.pose.orientation[3]
        );
    }

    getScene() {
        return this.scene;
    }

    getCamera() {
        return this.camera;
    }
}

export default LayaEngine;


